	module.exports = {"data":{"postsJson":{"title":"The King Enters Jerusalem","questionsTitle":"LEARNING FROM THE PAST","display":"block","bookName":"Matthew - ΚΑΤΑ ΜΑΘΘΑΙΟΝ","ref":"21:1-22:46","html":"<p>   <strong>21</strong><u>1</u>Καὶ ὅτε ἤγγισαν εἰς Ἱεροσόλυμα καὶ ἦλθον εἰς Βηθφαγὴ εἰς τὸ ὄρος τῶν ἐλαιῶν, τότε Ἰησοῦς ἀπέστειλεν δύο μαθητὰς <u>2</u>λέγων αὐτοῖς·</p><h1>JESUS</h1><h2>πορεύεσθε εἰς τὴν κώμην τὴν κατέναντι ὑμῶν, καὶ εὐθέως εὑρήσετε ὄνον δεδεμένην καὶ πῶλον μετʼ αὐτῆς· λύσαντες 1ἀγάγετέ μοι. <u>3</u>καὶ ἐάν τις ὑμῖν εἴπῃ τι, ἐρεῖτε ὅτι ὁ κύριος αὐτῶν χρείαν ἔχει· εὐθὺς δὲ ἀποστελεῖ αὐτούς.</h2><p><u>4</u>τοῦτο δὲ γέγονεν ἵνα πληρωθῇ τὸ ῥηθὲν διὰ τοῦ προφήτου λέγοντος·<br />   <u>5</u>εἴπατε τῇ θυγατρὶ Σιών·<br />   ἰδοὺ ὁ βασιλεύς σου ἔρχεταί σοι<br />   πραῢς καὶ ἐπιβεβηκὼς ἐπὶ ὄνον<br />   καὶ ἐπὶ πῶλον 1υἱὸν ὑποζυγίου.<br /><u>6</u>πορευθέντες δὲ οἱ μαθηταὶ καὶ ποιήσαντες καθὼς συνέταξεν αὐτοῖς ὁ Ἰησοῦς <u>7</u>ἤγαγον τὴν ὄνον καὶ τὸν πῶλον καὶ ἐπέθηκαν ἐπʼ αὐτῶν τὰ ἱμάτια, καὶ ἐπεκάθισεν ἐπάνω αὐτῶν. <u>8</u>ὁ δὲ πλεῖστος ὄχλος ἔστρωσαν ἑαυτῶν τὰ ἱμάτια ἐν τῇ ὁδῷ, ἄλλοι δὲ ἔκοπτον κλάδους ἀπὸ τῶν δένδρων καὶ ἐστρώννυον ἐν τῇ ὁδῷ. <u>9</u>οἱ δὲ ὄχλοι οἱ προάγοντες αὐτὸν καὶ οἱ ἀκολουθοῦντες ἔκραζον λέγοντες·</p><h1>THE CROWD</h1><h4>ὡσαννὰ τῷ υἱῷ Δαυίδ·<br />   εὐλογημένος ὁ ἐρχόμενος ἐν ὀνόματι κυρίου·<br />   ὡσαννὰ ἐν τοῖς ὑψίστοις.</h4><p>   <u>10</u>Καὶ εἰσελθόντος αὐτοῦ εἰς Ἱεροσόλυμα ἐσείσθη πᾶσα ἡ πόλις λέγουσα·</p><h1>THE CROWD</h1><h4>τίς ἐστιν οὗτος;</h4><p><u>11</u>οἱ δὲ ὄχλοι ἔλεγον·</p><h1>THE CROWD</h1><h4>οὗτός ἐστιν ὁ προφήτης Ἰησοῦς ὁ ἀπὸ Ναζαρὲθ τῆς Γαλιλαίας.</h4><p>   <u>12</u>Καὶ εἰσῆλθεν Ἰησοῦς εἰς τὸ ἱερὸν καὶ ἐξέβαλεν πάντας τοὺς πωλοῦντας καὶ ἀγοράζοντας ἐν τῷ ἱερῷ, καὶ τὰς τραπέζας τῶν κολλυβιστῶν κατέστρεψεν καὶ τὰς καθέδρας τῶν πωλούντων τὰς περιστεράς, <u>13</u>καὶ λέγει αὐτοῖς·</p><h1>JESUS</h1><h2>γέγραπται·<br />   ὁ οἶκός μου οἶκος προσευχῆς κληθήσεται,<br />   ὑμεῖς δὲ αὐτὸν ποιεῖτε σπήλαιον λῃστῶν.</h2><p><u>14</u>καὶ προσῆλθον αὐτῷ τυφλοὶ καὶ χωλοὶ ἐν τῷ ἱερῷ, καὶ ἐθεράπευσεν αὐτούς. <u>15</u>ἰδόντες δὲ οἱ ἀρχιερεῖς καὶ οἱ γραμματεῖς τὰ θαυμάσια ἃ ἐποίησεν καὶ τοὺς παῖδας τοὺς κράζοντας ἐν τῷ ἱερῷ καὶ λέγοντας·</p><h1>THE LITTLE CHILDREN</h1><h4>ὡσαννὰ τῷ υἱῷ Δαυίδ,</h4><p>ἠγανάκτησαν <u>16</u>καὶ εἶπαν αὐτῷ·</p><h1>JEWISH LEADERS</h1><h4>ἀκούεις τί οὗτοι λέγουσιν;</h4><p>ὁ δὲ Ἰησοῦς λέγει αὐτοῖς·</p><h1>JESUS</h1><h2>ναί. οὐδέποτε ἀνέγνωτε ὅτι<br />   ἐκ στόματος νηπίων καὶ θηλαζόντων κατηρτίσω αἶνον;</h2><p><u>17</u>καὶ καταλιπὼν αὐτοὺς ἐξῆλθεν ἔξω τῆς πόλεως εἰς Βηθανίαν καὶ ηὐλίσθη ἐκεῖ.<br />   <u>18</u>Πρωῒ δὲ ἐπανάγων εἰς τὴν πόλιν ἐπείνασεν. <u>19</u>καὶ ἰδὼν συκῆν μίαν ἐπὶ τῆς ὁδοῦ ἦλθεν ἐπʼ αὐτὴν καὶ οὐδὲν εὗρεν ἐν αὐτῇ εἰ μὴ φύλλα μόνον, καὶ λέγει αὐτῇ·</p><h1>JESUS</h1><h2>μηκέτι ἐκ σοῦ καρπὸς γένηται εἰς τὸν αἰῶνα.</h2><p>καὶ ἐξηράνθη παραχρῆμα ἡ συκῆ.<br />   <u>20</u>Καὶ ἰδόντες οἱ μαθηταὶ ἐθαύμασαν λέγοντες·</p><h1>THE DISCIPLES</h1><h3>πῶς παραχρῆμα ἐξηράνθη ἡ συκῆ;</h3><p><u>21</u>ἀποκριθεὶς δὲ ὁ Ἰησοῦς εἶπεν αὐτοῖς·</p><h1>JESUS</h1><h2>ἀμὴν λέγω ὑμῖν, ἐὰν ἔχητε πίστιν καὶ μὴ διακριθῆτε, οὐ μόνον τὸ τῆς συκῆς ποιήσετε, ἀλλὰ κἂν τῷ ὄρει τούτῳ εἴπητε· ἄρθητι καὶ βλήθητι εἰς τὴν θάλασσαν, γενήσεται· <u>22</u>καὶ πάντα ὅσα ἂν αἰτήσητε ἐν τῇ προσευχῇ πιστεύοντες λήμψεσθε.</h2><p>   <u>23</u>Καὶ ἐλθόντος αὐτοῦ εἰς τὸ ἱερὸν προσῆλθον αὐτῷ διδάσκοντι οἱ ἀρχιερεῖς καὶ οἱ πρεσβύτεροι τοῦ λαοῦ λέγοντες·</p><h1>JEWISH LEADERS</h1><h4>ἐν ποίᾳ ἐξουσίᾳ ταῦτα ποιεῖς; καὶ τίς σοι ἔδωκεν τὴν ἐξουσίαν ταύτην;</h4><p><u>24</u>ἀποκριθεὶς δὲ ὁ Ἰησοῦς εἶπεν αὐτοῖς·</p><h1>JESUS</h1><h2>ἐρωτήσω ὑμᾶς κἀγὼ λόγον ἕνα, ὃν ἐὰν εἴπητέ μοι κἀγὼ ὑμῖν ἐρῶ ἐν ποίᾳ ἐξουσίᾳ ταῦτα ποιῶ· <u>25</u>τὸ βάπτισμα τὸ Ἰωάννου πόθεν ἦν; ἐξ οὐρανοῦ ἢ ἐξ ἀνθρώπων;</h2><p>οἱ δὲ διελογίζοντο ἐν ἑαυτοῖς λέγοντες·</p><h1>JEWISH LEADERS</h1><h4>ἐὰν εἴπωμεν· ἐξ οὐρανοῦ, ἐρεῖ ἡμῖν· διὰ τί οὖν οὐκ ἐπιστεύσατε αὐτῷ; <u>26</u>ἐὰν δὲ εἴπωμεν· ἐξ ἀνθρώπων, φοβούμεθα τὸν ὄχλον, πάντες γὰρ ὡς προφήτην ἔχουσιν τὸν Ἰωάννην.</h4><p><u>27</u>καὶ ἀποκριθέντες τῷ Ἰησοῦ εἶπαν·</p><h1>JEWISH LEADERS</h1><h4>οὐκ οἴδαμεν.</h4><p>ἔφη αὐτοῖς καὶ αὐτός·</p><h1>JESUS</h1><h2>οὐδὲ ἐγὼ λέγω ὑμῖν ἐν ποίᾳ ἐξουσίᾳ ταῦτα ποιῶ.<br />   <u>28</u>Τί δὲ ὑμῖν δοκεῖ; ἄνθρωπος εἶχεν τέκνα δύο. καὶ προσελθὼν τῷ πρώτῳ εἶπεν· τέκνον, ὕπαγε σήμερον ἐργάζου ἐν τῷ ἀμπελῶνι.<u>29</u>ὁ δὲ ἀποκριθεὶς εἶπεν· οὐ θέλω, ὕστερον δὲ μεταμεληθεὶς ἀπῆλθεν.<u>30</u>προσελθὼν δὲ τῷ ἑτέρῳ εἶπεν ὡσαύτως. ὁ δὲ ἀποκριθεὶς εἶπεν· ἐγώ, κύριε, καὶ οὐκ ἀπῆλθεν. <u>31</u>τίς ἐκ τῶν δύο ἐποίησεν τὸ θέλημα τοῦ πατρός;</h2><p>λέγουσιν ·</p><h1>JEWISH LEADERS</h1><h4>ὁ πρῶτος.</h4><p>λέγει αὐτοῖς ὁ Ἰησοῦς·</p><h1>JESUS</h1><h2>ἀμὴν λέγω ὑμῖν ὅτι οἱ τελῶναι καὶ αἱ πόρναι προάγουσιν ὑμᾶς εἰς τὴν βασιλείαν τοῦ θεοῦ. <u>32</u>ἦλθεν γὰρ Ἰωάννης πρὸς ὑμᾶς ἐν ὁδῷ δικαιοσύνης, καὶ οὐκ ἐπιστεύσατε αὐτῷ, οἱ δὲ τελῶναι καὶ αἱ πόρναι ἐπίστευσαν αὐτῷ· ὑμεῖς δὲ ἰδόντες οὐδὲ μετεμελήθητε ὕστερον τοῦ πιστεῦσαι αὐτῷ.<br />   <u>33</u>Ἄλλην παραβολὴν ἀκούσατε. ἄνθρωπος ἦν οἰκοδεσπότης ὅστις ἐφύτευσεν ἀμπελῶνα καὶ φραγμὸν αὐτῷ περιέθηκεν καὶ ὤρυξεν ἐν αὐτῷ ληνὸν καὶ ᾠκοδόμησεν πύργον καὶ ἐξέδετο αὐτὸν γεωργοῖς καὶ ἀπεδήμησεν. <u>34</u>ὅτε δὲ ἤγγισεν ὁ καιρὸς τῶν καρπῶν, ἀπέστειλεν τοὺς δούλους αὐτοῦ πρὸς τοὺς γεωργοὺς λαβεῖν τοὺς καρποὺς αὐτοῦ. <u>35</u>καὶ λαβόντες οἱ γεωργοὶ τοὺς δούλους αὐτοῦ ὃν μὲν ἔδειραν, ὃν δὲ ἀπέκτειναν, ὃν δὲ ἐλιθοβόλησαν. <u>36</u>πάλιν ἀπέστειλεν ἄλλους δούλους πλείονας τῶν πρώτων, καὶ ἐποίησαν αὐτοῖς ὡσαύτως. <u>37</u>ὕστερον δὲ ἀπέστειλεν πρὸς αὐτοὺς τὸν υἱὸν αὐτοῦ λέγων· ἐντραπήσονται τὸν υἱόν μου. <u>38</u>οἱ δὲ γεωργοὶ ἰδόντες τὸν υἱὸν εἶπον ἐν ἑαυτοῖς· οὗτός ἐστιν ὁ κληρονόμος· δεῦτε ἀποκτείνωμεν αὐτὸν καὶ σχῶμεν τὴν κληρονομίαν αὐτοῦ, <u>39</u>καὶ λαβόντες αὐτὸν ἐξέβαλον ἔξω τοῦ ἀμπελῶνος καὶ ἀπέκτειναν. <u>40</u>ὅταν οὖν ἔλθῃ ὁ κύριος τοῦ ἀμπελῶνος, τί ποιήσει τοῖς γεωργοῖς ἐκείνοις;</h2><p><u>41</u>λέγουσιν αὐτῷ·</p><h1>JEWISH LEADERS</h1><h4>κακοὺς κακῶς ἀπολέσει αὐτοὺς καὶ τὸν ἀμπελῶνα ἐκδώσεται ἄλλοις γεωργοῖς, οἵτινες ἀποδώσουσιν αὐτῷ τοὺς καρποὺς ἐν τοῖς καιροῖς αὐτῶν.</h4><p><u>42</u>Λέγει αὐτοῖς ὁ Ἰησοῦς·</p><h1>JESUS</h1><h2>οὐδέποτε ἀνέγνωτε ἐν ταῖς γραφαῖς·<br />   λίθον ὃν ἀπεδοκίμασαν οἱ οἰκοδομοῦντες,<br />   οὗτος ἐγενήθη εἰς κεφαλὴν γωνίας·<br />   παρὰ κυρίου ἐγένετο αὕτη<br />   καὶ ἔστιν θαυμαστὴ ἐν ὀφθαλμοῖς ἡμῶν;<br />   <u>43</u>διὰ τοῦτο λέγω ὑμῖν ὅτι ἀρθήσεται ἀφʼ ὑμῶν ἡ βασιλεία τοῦ θεοῦ καὶ δοθήσεται ἔθνει ποιοῦντι τοὺς καρποὺς αὐτῆς. <u>44</u>καὶ ὁ πεσὼν ἐπὶ τὸν λίθον τοῦτον συνθλασθήσεται· ἐφʼ ὃν δʼ ἂν πέσῃ λικμήσει αὐτόν.</h2><p>   <u>45</u>Καὶ ἀκούσαντες οἱ ἀρχιερεῖς καὶ οἱ Φαρισαῖοι τὰς παραβολὰς αὐτοῦ ἔγνωσαν ὅτι περὶ αὐτῶν λέγει· <u>46</u>καὶ ζητοῦντες αὐτὸν κρατῆσαι ἐφοβήθησαν τοὺς ὄχλους, ἐπεὶ εἰς προφήτην αὐτὸν εἶχον.<br />   <strong>22</strong><u>1</u>Καὶ ἀποκριθεὶς ὁ Ἰησοῦς πάλιν εἶπεν ἐν παραβολαῖς αὐτοῖς λέγων·</p><h1>JESUS</h1><h2><u>2</u>ὡμοιώθη ἡ βασιλεία τῶν οὐρανῶν ἀνθρώπῳ βασιλεῖ, ὅστις ἐποίησεν γάμους τῷ υἱῷ αὐτοῦ. <u>3</u>καὶ ἀπέστειλεν τοὺς δούλους αὐτοῦ καλέσαι τοὺς κεκλημένους εἰς τοὺς γάμους, καὶ οὐκ ἤθελον ἐλθεῖν.<u>4</u>πάλιν ἀπέστειλεν ἄλλους δούλους λέγων· εἴπατε τοῖς κεκλημένοις· ἰδοὺ τὸ ἄριστόν μου ἡτοίμακα, οἱ ταῦροί μου καὶ τὰ σιτιστὰ τεθυμένα καὶ πάντα ἕτοιμα· δεῦτε εἰς τοὺς γάμους. <u>5</u>οἱ δὲ ἀμελήσαντες ἀπῆλθον, ὃς μὲν εἰς τὸν ἴδιον ἀγρόν, ὃς δὲ ἐπὶ τὴν ἐμπορίαν αὐτοῦ· <u>6</u>οἱ δὲ λοιποὶ κρατήσαντες τοὺς δούλους αὐτοῦ ὕβρισαν καὶ ἀπέκτειναν. <u>7</u>ὁ δὲ βασιλεὺς ὠργίσθη καὶ πέμψας τὰ στρατεύματα αὐτοῦ ἀπώλεσεν τοὺς φονεῖς ἐκείνους καὶ τὴν πόλιν αὐτῶν ἐνέπρησεν. <u>8</u>τότε λέγει τοῖς δούλοις αὐτοῦ· ὁ μὲν γάμος ἕτοιμός ἐστιν, οἱ δὲ κεκλημένοι οὐκ ἦσαν ἄξιοι· <u>9</u>πορεύεσθε οὖν ἐπὶ τὰς διεξόδους τῶν ὁδῶν καὶ ὅσους ἐὰν εὕρητε καλέσατε εἰς τοὺς γάμους. <u>10</u>καὶ ἐξελθόντες οἱ δοῦλοι ἐκεῖνοι εἰς τὰς ὁδοὺς συνήγαγον πάντας οὓς εὗρον, πονηρούς τε καὶ ἀγαθούς· καὶ ἐπλήσθη ὁ γάμος ἀνακειμένων. <u>11</u>Εἰσελθὼν δὲ ὁ βασιλεὺς θεάσασθαι τοὺς ἀνακειμένους εἶδεν ἐκεῖ ἄνθρωπον οὐκ ἐνδεδυμένον ἔνδυμα γάμου, <u>12</u>καὶ λέγει αὐτῷ· ἑταῖρε, πῶς εἰσῆλθες ὧδε μὴ ἔχων ἔνδυμα γάμου; ὁ δὲ ἐφιμώθη. <u>13</u>τότε ὁ βασιλεὺς εἶπεν τοῖς διακόνοις· δήσαντες αὐτοῦ πόδας καὶ χεῖρας ἐκβάλετε αὐτὸν εἰς τὸ σκότος τὸ ἐξώτερον· ἐκεῖ ἔσται ὁ κλαυθμὸς καὶ ὁ βρυγμὸς τῶν ὀδόντων. <u>14</u>πολλοὶ γάρ εἰσιν κλητοί, ὀλίγοι δὲ ἐκλεκτοί.</h2><p>   <u>15</u>Τότε πορευθέντες οἱ Φαρισαῖοι συμβούλιον ἔλαβον ὅπως αὐτὸν παγιδεύσωσιν ἐν λόγῳ. <u>16</u>καὶ ἀποστέλλουσιν αὐτῷ τοὺς μαθητὰς αὐτῶν μετὰ τῶν Ἡρῳδιανῶν λέγοντες·</p><h1>THE PHARISEES AND THE HERODIANS</h1><h4>διδάσκαλε, οἴδαμεν ὅτι ἀληθὴς εἶ καὶ τὴν ὁδὸν τοῦ θεοῦ ἐν ἀληθείᾳ διδάσκεις καὶ οὐ μέλει σοι περὶ οὐδενός· οὐ γὰρ βλέπεις εἰς πρόσωπον ἀνθρώπων, <u>17</u>εἰπὲ οὖν ἡμῖν τί σοι δοκεῖ· ἔξεστιν δοῦναι κῆνσον Καίσαρι ἢ οὔ;</h4><p><u>18</u>γνοὺς δὲ ὁ Ἰησοῦς τὴν πονηρίαν αὐτῶν εἶπεν·</p><h1>JESUS</h1><h2>τί με πειράζετε, ὑποκριταί; <u>19</u>ἐπιδείξατέ μοι τὸ νόμισμα τοῦ κήνσου.</h2><p>οἱ δὲ προσήνεγκαν αὐτῷ δηνάριον. <u>20</u>καὶ λέγει αὐτοῖς·</p><h1>JESUS</h1><h2>τίνος ἡ εἰκὼν αὕτη καὶ ἡ ἐπιγραφή;</h2><p><u>21</u>λέγουσιν αὐτῷ·</p><h1>THE PHARISEES AND THE HERODIANS</h1><h4>Καίσαρος.</h4><p>τότε λέγει αὐτοῖς·</p><h1>JESUS</h1><h2>ἀπόδοτε οὖν τὰ Καίσαρος Καίσαρι καὶ τὰ τοῦ θεοῦ τῷ θεῷ.</h2><p><u>22</u>καὶ ἀκούσαντες ἐθαύμασαν, καὶ ἀφέντες αὐτὸν ἀπῆλθαν.<br />   <u>23</u>Ἐν ἐκείνῃ τῇ ἡμέρᾳ προσῆλθον αὐτῷ Σαδδουκαῖοι, λέγοντες μὴ εἶναι ἀνάστασιν, καὶ ἐπηρώτησαν αὐτὸν <u>24</u>λέγοντες·</p><h1>THE SADDUCEES</h1><h4>διδάσκαλε, Μωϋσῆς εἶπεν· ἐάν τις ἀποθάνῃ μὴ ἔχων τέκνα, ἐπιγαμβρεύσει ὁ ἀδελφὸς αὐτοῦ τὴν γυναῖκα αὐτοῦ καὶ ἀναστήσει σπέρμα τῷ ἀδελφῷ αὐτοῦ. <u>25</u>ἦσαν δὲ παρʼ ἡμῖν ἑπτὰ ἀδελφοί· καὶ ὁ πρῶτος γήμας ἐτελεύτησεν, καὶ μὴ ἔχων σπέρμα ἀφῆκεν τὴν γυναῖκα αὐτοῦ τῷ ἀδελφῷ αὐτοῦ· <u>26</u>ὁμοίως καὶ ὁ δεύτερος καὶ ὁ τρίτος ἕως τῶν ἑπτά. <u>27</u>ὕστερον δὲ πάντων ἀπέθανεν ἡ γυνή. <u>28</u>ἐν τῇ ἀναστάσει οὖν τίνος τῶν ἑπτὰ ἔσται γυνή; πάντες γὰρ ἔσχον αὐτήν·</h4><p><u>29</u>Ἀποκριθεὶς δὲ ὁ Ἰησοῦς εἶπεν αὐτοῖς·</p><h1>JESUS</h1><h2>πλανᾶσθε μὴ εἰδότες τὰς γραφὰς μηδὲ τὴν δύναμιν τοῦ θεοῦ· <u>30</u>ἐν γὰρ τῇ ἀναστάσει οὔτε γαμοῦσιν οὔτε γαμίζονται, ἀλλʼ ὡς ἄγγελοι ἐν τῷ οὐρανῷ εἰσιν.<u>31</u>περὶ δὲ τῆς ἀναστάσεως τῶν νεκρῶν οὐκ ἀνέγνωτε τὸ ῥηθὲν ὑμῖν ὑπὸ τοῦ θεοῦ λέγοντος· <u>32</u>ἐγώ εἰμι ὁ θεὸς Ἀβραὰμ καὶ ὁ θεὸς Ἰσαὰκ καὶ ὁ θεὸς Ἰακώβ; οὐκ ἔστιν ὁ θεὸς νεκρῶν ἀλλὰ ζώντων.</h2><p><u>33</u>καὶ ἀκούσαντες οἱ ὄχλοι ἐξεπλήσσοντο ἐπὶ τῇ διδαχῇ αὐτοῦ.<br />   <u>34</u>Οἱ δὲ Φαρισαῖοι ἀκούσαντες ὅτι ἐφίμωσεν τοὺς Σαδδουκαίους συνήχθησαν ἐπὶ τὸ αὐτό, <u>35</u>καὶ ἐπηρώτησεν εἷς ἐξ αὐτῶν νομικὸς πειράζων αὐτόν·</p><h1>A RELIGIOUS TEACHER</h1><h4><u>36</u>διδάσκαλε, ποία ἐντολὴ μεγάλη ἐν τῷ νόμῳ;</h4><p><u>37</u>ὁ δὲ ἔφη αὐτῷ·</p><h1>JESUS</h1><h2>ἀγαπήσεις κύριον τὸν θεόν σου ἐν ὅλῃ τῇ καρδίᾳ σου καὶ ἐν ὅλῃ 1τῇ ψυχῇ σου καὶ ἐν ὅλῃ τῇ διανοίᾳ σου· <u>38</u>αὕτη ἐστὶν ἡ μεγάλη καὶ πρώτη ἐντολή. <u>39</u>δευτέρα δὲ ὁμοία αὐτῇ· ἀγαπήσεις τὸν πλησίον σου ὡς σεαυτόν. <u>40</u>ἐν ταύταις ταῖς δυσὶν ἐντολαῖς ὅλος ὁ νόμος κρέμαται καὶ οἱ προφῆται.</h2><p><u>41</u>Συνηγμένων δὲ τῶν Φαρισαίων ἐπηρώτησεν αὐτοὺς ὁ Ἰησοῦς <u>42</u>λέγων·</p><h1>JESUS</h1><h2>τί ὑμῖν δοκεῖ περὶ τοῦ χριστοῦ; τίνος υἱός ἐστιν;</h2><p>λέγουσιν αὐτῷ·</p><h1>THE PHARISEES</h1><h4>τοῦ Δαυίδ.</h4><p><u>43</u>λέγει αὐτοῖς·</p><h1>JESUS</h1><h2>πῶς οὖν Δαυὶδ ἐν πνεύματι καλεῖ αὐτὸν κύριον λέγων·<br />   <u>44</u>εἶπεν κύριος τῷ κυρίῳ μου·<br />   κάθου ἐκ δεξιῶν μου,<br />   ἕως ἂν θῶ τοὺς ἐχθρούς σου<br />   ὑποκάτω τῶν ποδῶν σου;<br />   <u>45</u>εἰ οὖν Δαυὶδ καλεῖ αὐτὸν κύριον, πῶς υἱὸς αὐτοῦ ἐστιν;</h2><p><u>46</u>καὶ οὐδεὶς ἐδύνατο ἀποκριθῆναι αὐτῷ λόγον οὐδὲ ἐτόλμησέν τις ἀπʼ ἐκείνης τῆς ἡμέρας ἐπερωτῆσαι αὐτὸν οὐκέτι.</p><h5>_______________</h5>","path":"/311","Question1":"With which character do you most identify in this story ? Why?","Question2":"What strengths and/or weaknesses do you see in this character?","Question3":"What do you think God was wanting your selected character to learn?","Question4":"What do you think God is wanting you to learn through this story? How can you cooperate with his good purposes today?","prevPath":"/310","nextPath":"/312"}},"pathContext":{}}

